import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'editortype'
})
export class EditTableEditorTypePipe implements PipeTransform {
    transform(data: any, ...args: any[]): any {
        const col = args[0];
        if (col.editor) {
            return col.editor.type || 'text';
        }

        if (data) {
            if (data.$editors) {
                if (col && data.$editors[col.field]) {
                    return data.$editors[col.field].type || 'text';
                }
                return 'text';
            }
            return 'text';
        }

        return null;
    }
}


@Pipe({
    name: 'editoropts'
})
export class EditTableEditorOptionsPipe implements PipeTransform {
    transform(data: any, ...args: any[]): any {
        const col = args[0];
        if (col.editor) {
            return col.editor.options || null;
        }

        if (data) {
            if (data.$editors) {
                if (col) {
                    return data.$editors[col.field] ? data.$editors[col.field].options : null;;
                }
                return null;
            }
            return null;
        }

        return null;
    }
}
