import { ChangeDetectorRef, Component, EventEmitter, Injector, Input, OnInit, Output } from "@angular/core";
import { IdService } from "@farris/ui-common";
import { MessagerService } from "@farris/ui-messager";
import { NotifyService } from "@farris/ui-notify";

import { EditTableColumn } from "./edit-table.types";

@Component({
    selector: "farris-edit-table",
    templateUrl: './edit-table.component.html',
    styles: [],
})
export class EditTableComponent implements OnInit {

    @Input() columns: Array<EditTableColumn>;
    @Input() data = [];

    @Input() selectId = '';

    /** è¡æ°æ®æ è¯å­æ®µ */
    @Input() idField = '_id';

    @Output() dataChange = new EventEmitter();

    private selectedItem: { index: number, data: any } = null;

    constructor(private injector: Injector, private idSer: IdService, private cdr: ChangeDetectorRef, private msgSer: MessagerService, private notifySer: NotifyService) {}

    ngOnInit() {}


    private dataChanged() {
        this.dataChange.emit(this.data);
    }


    private createNewData() {
        return this.columns.reduce((p, n) => {
            if (n.field.indexOf('.') > -1) {
                p = Object.assign(p, n.defaultValue);
            } else {
                p[n.field] = n.defaultValue || null;
            }

            return p;
        }, {_id: this.idSer.guid() })
    }

    /** æå¥æ°è¡ */
    insertRow(index: number, $event?: MouseEvent) {
        if ($event) {
            $event.stopPropagation();
        }
        const newItem = this.createNewData();
        const _newItem = Object.assign({}, newItem);
        
        this.data.splice(index + 1, 0, _newItem);
        this.dataChanged();
    }

    /** æ°å¢è¡ */
    appendNew($event: MouseEvent) {
        $event.stopPropagation();
        const newItem = this.createNewData();
        this.data = [...this.data, newItem];
        this.dataChanged();
    }

    /** éä¸­è¡ */
    selectRow($event: MouseEvent, rowData, index) {
        if ($event) {
            $event.stopPropagation();
        }
        this.selectId = rowData[this.idField];
        this.selectedItem  = {
            data: rowData,
            index
        };
    }

    deleteRow($event: MouseEvent, index) {
        if ($event) {
            $event.stopPropagation();
        }
        this.data.splice(index, 1);
        this.dataChanged();

        if (this.selectedItem) {
            if (index === this.selectedItem.index) {
                
                if ( this.data[index] ) {
                    this.selectedItem = { index: index, data: this.data[index] };
                } else {
                    if ( this.data[index - 1] ) {
                        this.selectedItem = { index: index - 1, data: this.data[index - 1] };
                    } else {
                        this.selectedItem = null;
                    }
                }
            } else {
                if (!this.data[this.selectedItem.index]) {
                    if (this.selectedItem.index > index) {
                        this.selectedItem.index = this.selectedItem.index - 1;
                    } else {
                        this.selectedItem.index = this.selectedItem.index + 1 ;
                    }
                }
            }

            this.cdr.detectChanges();
        }
    }

    clear($event) {
        this.msgSer.question('ç¡®è®¤è¦æ¸ç©ºå½åæææ¡ä»¶åï¼', () => {
            this.data = [];
            this.selectedItem = null;
            this.selectId = '';
            this.dataChanged();
        });
    }

    moveRow($event, action: 'top'|'bottom'|'up'|'down') {
        if ($event) {
            $event.stopPropagation();
        }

        if (!this.selectedItem) {
            this.notifySer.warning('è¯·éæ©è¦ç§»å¨çè¡');
            return;
        }

        const index = this.selectedItem.index;
        let tempArr = [];
        switch(action) {
            case 'top': 
                this.data.unshift(this.selectedItem.data);
                this.data.splice(index + 1, 1);
                this.selectedItem.index = 0;
                break;
            case 'bottom':
                this.data.push(this.selectedItem.data);
                this.data.splice(index, 1);
                this.selectedItem.index = this.data.length - 1;
                break;
            case 'up':
                tempArr = this.data.splice(index, 1);
                this.data.splice(index - 1, 0, ...tempArr);
                this.selectedItem.index = index - 1;
                break;
            case 'down':
                tempArr = this.data.splice(index, 1);
                this.data.splice(index + 1, 0, ...tempArr);
                this.selectedItem.index = index + 1;
                break;
        }

        this.dataChanged();  
    }

    onComboLookupValueChange($event, ctx) {
        if (ctx && ctx.options && ctx.options.valueChange) {
            const {rowData, field}  = ctx;
            ctx.options.valueChange({...$event, data: rowData, field});
        }
    }
}
